<script>
class MyCircularQueue {
  constructor(k) {
    this.k = k
    this.head = -1
    this.tail = -1
    this.size = 0
    this.data = []
  }
  Front() { // 获取队列头部, 为空则返回-1
    return this.size === 0 ? -1 : this.data[this.head]
  }
  Rear() { // 获取队列尾部, 为空则返回-1
    return this.size === 0 ? -1 : this.data[this.tail]
  }
  enQueue(value) { // 入队
    if (this.size === this.k) return false
    if (this.size === 0) this.head++
    if (this.tail === this.k-1) {
      this.tail = 0
    } else {
      this.tail++
    }
    this.size++
    this.data[this.tail] = value
    return true
  }
  deQueue() { // 出队
    if (this.size === 0) return false
    this.data[this.head] = undefined
    if (this.head === this.k-1) {
      this.head = 0
    } else {
      this.head++
    }
    this.size--
    if (this.size === 0) {
      this.head = -1
      this.tail = -1
    }
    return true
  }
  isEmpty() { // 检查循环队列是否为空
    return this.size === 0
  }
  isFull() { // 检查循环队列是否已满
    return this.size === this.k
  }
}
export default {
  data () {
    return {
      show: false,
      list: [],
      head: -1,
      tail: -1,
      currIndex: 0,
      rate: 80,                   // 转盘速率
      radius: 180,                // 转盘半径
      textFontSize: '13px',       // 文字大小
      lineHeight: 20,             // 文字行高
      textColor: '#d64737',       // 文字颜色
      textMargin: 30,             // 文字距离边框距离
      textPadding: 0,             // 文字补偿宽度
      btnFontSize: '26px',        // 按钮文字大小
      btnColor: '#d64737',        // 按钮文件颜色
      btnBorderColor1: '#d64737', // 按钮外边框颜色
      btnBorderColor2: '#ffffff', // 按钮内边框颜色
      btnBorderColor3: '#f6c66f', // 按钮指针颜色
      btnBgColor: '#ffdea0',      // 按钮背景颜色
      btnText: '抽奖',            // 按钮内容
      btnRadius: 60,              // 按钮半径
      borderColor: '#d64737',     // 边框颜色
      awards: [                   // 奖品
        { name: '价值5988元华为 P30pro', color: '#f9e3bb' },
        { name: '价值398元车载空气净化器', color: '#f8d384' },
        { name: '价值25元百叶帘遮阳挡', color: '#f9e3bb' },
        { name: '16元油卡套餐红包', color: '#f8d384' },
        { name: '5元油卡直冲红包', color: '#f9e3bb' },
        { name: '3元话费直冲红包', color: '#f8d384' },
        { name: '价值32元重力感应手机支架', color: '#f9e3bb' },
        { name: '价值198元手提迷你车在保温冷藏箱', color: '#f8d384' },
      ],
    }
  },
  mounted () {
    this.q = new MyCircularQueue(5)
  },
  updated () {
    if (this.timer) clearTimeout(this.timer)
    this.timer = setTimeout(() => {
      this.$refs.LuckDraw.initCanvas()
    }, 400)
  },
  methods: {
    add () {
      this.q.enQueue(Math.floor(Math.random()*10))
      console.log(this.q)
      this.comp()
    },
    exit () {
      this.q.deQueue()
      console.log(this.q)
      this.comp()
    },
    comp () {
      this.list = this.q.data
      this.head = this.q.head
      this.tail = this.q.tail
    },
    handleStart () {
      console.log('开始抽奖')
      this.show = true
    },
    handleEnd (index) {
      alert('恭喜您抽到大奖, 奖品为' + this.awards[this.currIndex].name)
      this.show = false
    },
    selectAwards (e) {
      this.currIndex = Number(e.target.value)
    }
  }
}
</script>


